.about-section {
    padding: 0 32px 32px 80px;
    display: flex;
    justify-content: left;
    align-items: left;
    gap: 24px;
    background: none;
}
/* Image */
.about-image-content {
    width: 30%;
    max-width: 100%;
    text-align: center;
}
.about-image1 {
    width: 80%;
    height: auto;
}
/* Text */
.about-text-content {
    width: 50%;
}
.about-title {
    font-family: 'Montserrat', sans-serif;
    font-size: 40px;
    font-weight: bold;
}

.about-description {
    margin: 32px 0;
    color: black;
    font-family: 'Montserrat', sans-serif;
    font-size: 18px;
    letter-spacing: .7px;
    line-height: 1.7rem;
}
.about-text-title {
    margin: 0 0 24px 0;
    color: black;
    font-family: 'Montserrat', sans-serif;
    font-size: 24px;
    font-weight: bold;
    letter-spacing: .7px;
}
/* Steps */
.about-text-step {
    margin: 16px 0;
}
.about-text-sTitle {
    font-family: 'Montserrat', sans-serif;
    font-size: 18px;
    font-weight: bold;
    letter-spacing: .7px;
}
.about-text-description {
    margin: 8px 24px;
    color: #4d4b4b;
    color: black;
    font-family: 'Montserrat', sans-serif;
    letter-spacing: .7px;
    line-height: 1.6rem;
}
.fa-icon {
    color: #9D2553;
}
/* Responsive */
@media screen and (max-width: 1000px) {
    
    .about-title {
        font-size: 32px; 
        padding-bottom: 20px; 
    }
    .about-section {
        display: block;
        
    }
    .about-image-content {
        width: 100%;
        max-width: 100%;
        margin: 0 0 32px 0;
    }
    .about-image1 {
        width: 100%;
        height: 100%;
    }
    .about-text-content {
        width: 100%;
    }
}
@media screen and (max-width: 700px) {
    .about-text-content {
        width: 100%;
        padding-top: 5px;
        margin-top: 20px;
    }

}
