.ba-section {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 24px;
    background-color: white;
}
/* Image */
.ba-image-content {
    width: 50%;
    max-width: 100%;
    text-align: center;
}
.ba-image1 {
    width: 80%;
    height: auto;
}
/* Text */
.ba-text-content {
    width: 100%;
    padding: 10px 40px 10px 40px;
}
.ba-title {
    font-family: 'Montserrat', sans-serif;
    font-size: 35px;
    font-weight: bold;
    letter-spacing: .7px;
}
.ba-title > span {
    /*position: relative;*/
    border-bottom: 4px solid #9D2553;
}
