.ba-description {
    margin: 32px 0;
    color: #4d4b4b;
    font-family: 'Montserrat', sans-serif;
    font-size: 18px;
    letter-spacing: .8px;
    line-height: 1.8rem;
}
/* Checks */
.ba-checks {
    margin: 28px 0;
    font-family: 'Montserrat', sans-serif;
    font-size: 22px;
    font-weight: normal;
    letter-spacing: .7px;
}
.ba-check-first {
    margin-top: 40px;
}
.ba-check-last {
    margin-bottom: 40px;
}
/* Book Appointment Button */
.ba-appointment-btn {
    padding: 18px 24px;
    color: white;
    border: 1px solid transparent;
    border-radius: 50px;
    outline: transparent;
    background-color: #1A8EFD;
    font-size: 20px;
    font-family: 'Montserrat', sans-serif;
    letter-spacing: .8px;
    cursor: pointer;
    transition: all .4s ease;
}
.ba-appointment-btn:hover {
    color: #1A8EFD;
    background-color: transparent;
    border: 1px solid #1A8EFD;
}
/* Responsive */
@media screen and (max-width: 1000px) {
    .ba-section {
        display: block;
    }
    .ba-image-content {
        width: 100%;
        max-width: 100%;
        margin: 0 0 32px 0;
    }
    .ba-text-content {
        width: 100%;
    }
    .ba-image1 {
        width: 80%;
        height: 80%;
    }
    .ba-checks {
        font-size: 16px;
        
    }
    .ba-check-first {
        font-size: 16px;
        
    }
    .ba-check-last {
        font-size: 16px;
        
    }
    .ba-title {
        font-size: 32px; 
        padding-bottom: 20px; 
    }
}
